import React, { useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";

const GalleryPage = () => {
    const { pageNumber } = useParams();

    const importAll = (require) => {
        return require.keys().map(require)
    }
    const images = importAll(require.context('../../public/assets/images/imageGallery', false, /\.(png|jpe?g|svg)$/));

    const currentPage = parseInt(pageNumber ? pageNumber:1, 10);
    
    const ImagesPerPage = 16;
    const startIndex = (currentPage - 1) * ImagesPerPage;
    const endIndex = startIndex + ImagesPerPage;

    const slicedImages = images.slice(startIndex, endIndex);

    const totalPages = Math.ceil(images.length / ImagesPerPage);

    const navigate = useNavigate()

    const handlePageChange=(page)=>{
        navigate(`/gallery/${page}`)
    }

    const getRandomAngle=()=>{
        const randomValue = Math.random();
      
        // Convert the random value to an angle between 0 and 90 degrees
        const angle = randomValue * 5;
        const translateY = randomValue * 5
      
        return {angle:angle, translateY:translateY};
      }

    const handleWindowClick=()=>{
        document.querySelectorAll(".expandImage").forEach((ele)=>ele.className="")
        document.body.style.overflow = 'unset';
    }

    useEffect(()=>{
        window.addEventListener("click", handleWindowClick)

        return ()=>{
            window.removeEventListener("click", handleWindowClick)
        }
    })

    return (<>
        <div className="galleryPage">
            {slicedImages.map((image, index) => (
                <img 
                    key={index}
                    style={{
                        transform: `rotate(${getRandomAngle().angle}deg) translateY(${getRandomAngle().translateY}px)`
                    }}
                    src={image} 
                    alt={`Image ${index}`}
                    loading="lazy"
                    onClick={(e) => {
                        e.stopPropagation();
                        const expandImage = e.target;
                        
                        document.querySelectorAll(".expandImage").forEach((ele) => {
                            ele.className = "";
                        });
                    
                        expandImage.className = "expandImage";
                        document.body.style.overflow = 'hidden';

                        expandImage.addEventListener("animationend", () => {
                            expandImage.scrollIntoView({
                                behavior: 'smooth',
                                block: 'center',
                            });
                        }, { once: true });
                    }}
                    />
            ))}
            <div className="pagination">
                {Array.from({ length: totalPages }, (_, index) => (
                    <button
                        key={index}
                        onClick={() => {handlePageChange(index + 1)}}
                        className={currentPage === index + 1 ? 'active' : ''}
                        style={index+1==pageNumber ? {
                            textDecoration: "underline"
                        }:{}}
                    >
                        {index + 1}
                    </button>
                ))}
            </div>
        </div>
    </>)
}

export default GalleryPage